import React, { Component } from "react";
import { Empty, Input, Spin, Alert } from "antd";
import Posts from "../components/Posts";
import api from "../api";

const { Search } = Input;

export default class SearchContainer extends Component {
  state = {
    isLoading: false,
    results: []
  };

  getData = async value => {
    const data = await api.getSearchData(value);
    await this.setState({ results: data.data, isLoading: false });
  };

  handleSearch = value => {
    this.setState({ isLoading: true });
    this.getData(value);
  };

  render() {
    const { isLoading, results } = this.state;
    return (
      <>
        <Search
          placeholder="ê²ìì´ë¥¼ ìë ¥í´ì£¼ì¸ì."
          onSearch={value => this.handleSearch(value)}
          enterButton
          size="large"
        />
        {isLoading ? (
          <Spin tip="Loading...">
            <Alert
              message="ë°ì´í°ë¥¼ ë¶ë¬ì¤ë ì¤"
              description="ìê°ì´ ì¤ë ê±¸ë¦°ë¤ë©´ ìë²ê° ë«íìë ê±°ëëë¤."
              type="info"
            />
          </Spin>
        ) : (
          <div className="posts">
            {results.map(item => (
              <Posts
                id={item.id}
                author={item.author}
                content={item.content}
                key={item.id}
              />
            ))}
          </div>
        )}
        {results.length === 0 ? <Empty style={{ marginTop: "75px" }} /> : ""}
      </>
    );
  }
}
